import Error from "../Pages/Error";
import Home from "../Pages/Home";
import LinuxSystem from "../Pages/LinuxSystem";
import WindowsSystem from "../Pages/WindowsSystem";
import { Navigate } from "react-router-dom";
import ZKsync from "../Pages/ZKsync";
import Layer0 from "../Pages/Layer0";

export const routers =[
    { exact: true, path: "/how-i-set-io", element: <Home />, key: "0.1"}, 
    { exact: true, path: "", element: <Home/> , key: "1" },
    { exact: true, path: "/home", element: <Home/> , key: "2" },
    { exact: true, path: "/windows", element: <WindowsSystem/> , key: "3" },
    { exact: true, path: "/linux", element: <LinuxSystem/> , key: "4" },
    { exact: true, path: "/error", element: <Error/> , key: "5" },
    { exact: true, path: "/layer0", element: <Layer0/> , key: "6" },
    { exact: true, path: "/zksync", element: <ZKsync/> , key: "7" },
    { exact: true, path: "*", element: <Navigate to="/error" replace />, key: "end" }
]
