import React, { useEffect, useState } from 'react';
import cl from '../css/home.module.css'
import DelayLink from "../components/DelayLink";


const Home = () => {
  const [helloImage, setHelloImage] = useState({opacity:0});
  const [hideHello, setHideHello] = useState({display: 'block'})

   useEffect(()=>{
     setTimeout(() => setHelloImage({opacity:1}), 2500);
     setTimeout(() => setHideHello({ opacity: 0 }), 3500);
     setTimeout(() => setHideHello({ display: "none" }), 5500);
   },[])




  function handleClick(event) {
    event.currentTarget.classList.toggle("transition"); 
  }

   const active=()=> { 
   }
  
      const end=()=> {  
   }
  

    return (
      <div className={["content", cl.content_home].join(" ")}>
        <div style={hideHello} className={cl.block_hello}>
          <p className={[cl.cursor, cl.typewriter_animation].join(" ")}>
             
          </p>

          <div
            style={ helloImage }
            className={cl.block_hello_image}
          ></div>
        </div>

        <div className={cl.what_system}>
          <div className={cl.system_box}>
            <h3 style={{ textAlign: "center" }}></h3>
            <div
              onClick={handleClick}
              className={[cl.system_windows, cl.system_box_buttom].join(" ")}
            >
              <DelayLink
                to="/windows"
                delay={500}
                replace={true}
                onDelayStart={active}
                onDelayEnd={end}
              />
            </div>
          </div>
          <div className={cl.system_box}>
            <h3 style={{ textAlign: "center" }}></h3>
            <div
              onClick={handleClick}
              className={[cl.system_linux, cl.system_box_buttom].join(" ")}
            >
              <DelayLink
                to="/linux"
                delay={500}
                replace={true}
                onDelayStart={active}
                onDelayEnd={end}
              />
            </div>
          </div>
        </div>
      </div>
    );
};

export default Home;