import React, { useEffect, useState } from "react";
import cl from "./blockText.module.css";
import Aos from "aos";
import "aos/dist/aos.css";

const TextBlock = (props) => {

     useEffect(() => {
       Aos.init({ duration: 1500 });
     }, []);

       function handleClick(event) {
         event.currentTarget.classList.toggle("conteiner_box_shift");
       }
  return (
    <div onClick={handleClick} className={cl.conteiner_box}>
      <div style={{top: props.top}} className={cl.button_return}>{props.num}</div>
      <div className="content">
        <div className={cl.conteiner_box_context}>
          <h3 data-aos="fade-right">{props.title}</h3>
          <span data-aos="fade-right">{props.text[1]}</span>
          <span data-aos="fade-right">{props.text[2]}</span>
          <span data-aos="fade-right">{props.text[3]}</span>
          <span data-aos="fade-right">{props.text[4]}</span>
          <span data-aos="fade-right">{props.text[5]}</span>
          <span data-aos="fade-right">{props.text[6]}</span>
          <span data-aos="fade-right">{props.text[7]}</span>
        </div>
      </div>
    </div>
  );
   
};

export default TextBlock;
