import React, { useState } from 'react';
import Cube from "react-cube3d-component";
import cl from "./Box.module.css"
import DelayLink from '../DelayLink';
import { Link } from 'react-router-dom';

const Box = () => {
  const [active, setActive] = useState("")

  const addClass =()=> {
    setActive(cl.active)
  }

/*  
function handleClick(event) {
        event.currentTarget.parentNode.classList.toggle("transition");
      }
 */
    

    return (
      <div className={cl.cube_conteiner}>
        <h2>ZKsync</h2>
        <h2>Layer-Zero </h2>

        <div
          className={[cl.cube, active].join(" ")}
          style={{
            width: 300,
            height: 300,
          }}
        >
          <Cube cubeSize="300px" viewportSize="300px">
          
              <div className={[cl.front, cl.remains].join(" ")}>
                <span onClick={addClass}>
                  <DelayLink to="/zksync" delay={500} replace={true}>
                     ZKsync
                  </DelayLink>
                </span>
              </div>
    

       
              <div className={[cl.right, cl.remains].join(" ")}>
                <span onClick={addClass}>
                  <DelayLink to="/layer0" delay={500} replace={true}>
                     Layer-Zero
                  </DelayLink>
                </span>
              </div>
      

          
              <div className={[cl.back, cl.remains].join(" ")}>
                <span onClick={addClass}>
                  <DelayLink to="/zksync" delay={500} replace={true}>
                     ZKsync
                  </DelayLink>
                </span>
              </div>
    

            
              <div className={[cl.left, cl.remains].join(" ")}>
                <span onClick={addClass}>
                  <DelayLink to="/layer0" delay={500} replace={true}>
                     Layer-Zero
                  </DelayLink>
                </span>
              </div>
       

      
              <div className={[cl.top, cl.remains].join(" ")}>
                <span onClick={addClass}>
                  <DelayLink to="/zksync" delay={500} replace={true}>
                     ZKsync
                  </DelayLink>
                </span>
              </div>
            
         
              <div className={[cl.bottom, cl.remains].join(" ")}>
                <span onClick={addClass}>
                  <DelayLink to="/layer0" delay={500} replace={true}>
                     Layer-Zero
                  </DelayLink>
                </span>
              </div>
           
          </Cube>

        </div>
      </div>
    );
};

export default Box;